.footer {
  background-color: $defaultColor;
  .footer-info {
    @include flexMixin(row, space-between, center);
    padding: 68px 0px;
    .navigation {
      @include flexMixin(row, normal, center);
      .links-list {
        @include flexMixin(row, normal, center);
        flex-wrap: wrap;
        .link {
          margin-right: 15px;
          a {
            transition: color 0.2s linear;
            @include textMixin($robotoFont, #595959, 18px, 21px);
            &:hover {
              transition: color 0.2s linear;
              color: #ffc107;
            }
          }
        }
      }
      img {
        margin-right: 35px;
      }
    }
    .contact-us {
      @include flexMixin(row, normal, center);
      a {
        transition: all 0.2s linear;
        margin-right: 21px;
        &:hover {
          transition: all 0.2s linear;
          transform: scale(1.06);
        }
      }
    }
  }
}
@media (max-width: 1100px) {
  .footer {
    .footer-info {
      .navigation {
        .links-list {
          .link {
            a {
              font-size: 16px;
            }
          }
        }
      }
    }
  }
}
@media (max-width: 768px) {
  .footer {
    .footer-info {
      .navigation {
        .links-list {
          .link {
            flex-basis: 30%;
          }
        }
      }
    }
  }
}
@media (max-width: 690px) {
  .footer {
    .footer-info {
      @include flexMixin(column, normal, center);
      padding: 25px 0px;
      .navigation {
        @include flexMixin(column, normal, center);
        margin-bottom: 25px;
        img{
            margin-right: 0;
            margin-bottom: 25px;
        }
        .links-list {
          @include flexMixin(column, normal, center);
          .link {
            flex-basis: 20%;
            margin-right: 0;
            margin-bottom: 15px;
          }
        }
      }
    }
  }
}
