@mixin flexMixin($direction, $justify, $align) {
  display: flex;
  flex-direction: $direction;
  justify-content: $justify;
  align-items: $align;
}

@mixin textMixin($family, $color, $size, $height) {
  font-family: $family;
  color: $color;
  font-size: $size;
  line-height: $height;
}
