.main {
  .intro {
    box-shadow: 0px 7px 12px rgba(158, 158, 163, 0.1);
    height: 300px;
    background-color: #FFF1B8;
    border-radius: 10px;
    background-image: url(../img/pizza.png);
    background-repeat: no-repeat;
    background-position: top -10px right -160px;
    background-size: 570px;
    margin-bottom: 56px;
    .intro-info {
      position: relative;
      width: 43%;
      left: 7%;
      top: 20%;
      .intro-header {
        @include textMixin($robotoFont, $ordinaryTextColor, 39px, 46px);
        font-weight: bold;
        margin-bottom: 15px;
      }
      .intro-text{
        @include textMixin($robotoFont,$ordinaryTextColor,24px,28px);
      }
    }
  }
}
@media (max-width: 1100px) {
 .main{
   .intro{
     .intro-info{
       .intro-header{
          font-size: 30px;
       }
       .intro-text{
          font-size: 18px;
       }
     }
   }
 }
}
@media (max-width: 768px) {
  .main{
    .intro{
      background-size: 450px;
      background-position: top -30px right -160px;
      .intro-info{
        .intro-header{
           font-size: 25px;
           line-height: 30px;
        }
        .intro-text{
           font-size: 16px;
        }
      }
    }
  }
}
@media (max-width: 690px) {
  .main{
    .intro{
      background-size: 350px;
      background-position: top -20px right -140px;
      .intro-info{
        .intro-header{
           font-size: 20px;
           line-height: 25px;
        }
        .intro-text{
           font-size: 14px;
        }
      }
    }
  }
}
@media (max-width: 500px) {
  .main{
    .intro{
      background-size: 250px;
      background-position: top -60px right -100px;
      .intro-info{
        width: 50%;
        top: 17%;
        left: 10%;
      }
    }
  }
}

