body {
  text-align: left;
  background: #fff;
  color: #000;
}

sup,
sub {
  vertical-align: inherit;
}

body,
h1,
h2,
h3,
h4,
h5,
h6,
hr,
p,
figure,
blockquote,
pre,
ol,
ul,
dl,
dd,
fieldset {
  margin: 0;
}

ol,
ul,
th,
td,
legend {
  padding: 0;
}

h1,
h2,
h3,
h4,
h5,
h6,
address,
pre,
th,
em,
strong,
i,
small,
dfn,
b,
var,
cite,
sup,
sub,
code,
kbd,
samp {
  font: inherit;
}

caption,
th {
  text-align: inherit;
}

pre {
  white-space: inherit;
}

mark {
  background: transparent;
}

a,
u,
s,
ins,
del {
  text-decoration: none;
}

ol,
ul {
  list-style: none;
}

q {
  quotes: none;
}

a,
mark {
  color: inherit;
}

/* roboto-regular - cyrillic */
@font-face {
  font-family: 'Roboto';
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url("../fonts/roboto-v20-cyrillic-regular.eot");
  /* IE9 Compat Modes */
  src: local("Roboto"), local("Roboto-Regular"), url("../fonts/roboto-v20-cyrillic-regular.eot?#iefix") format("embedded-opentype"), url("../fonts/roboto-v20-cyrillic-regular.woff2") format("woff2"), url("../fonts/roboto-v20-cyrillic-regular.woff") format("woff"), url("../fonts/roboto-v20-cyrillic-regular.ttf") format("truetype"), url("../fonts/roboto-v20-cyrillic-regular.svg#Roboto") format("svg");
  /* Legacy iOS */
}

.header {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  margin-top: 44px;
  margin-bottom: 40px;
}

.header .logo {
  transition: all 0.3s linear;
}

.header .logo::before {
  content: "";
  display: inline-block;
  background-color: #ffc107;
  width: 3px;
  height: 0px;
  margin-right: 5px;
}

.header .logo:hover::before {
  transition: all 0.3s linear;
  height: 35px;
}

.header .search {
  flex: 0.8;
  height: 40px;
  background-image: url(../img/home.svg);
  background-repeat: no-repeat;
  background-position: 10px center;
}

.buttons {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

.buttons .grey-btn,
.buttons .basket-btn {
  background-color: transparent;
  box-shadow: 0px 0px 2px rgba(0, 0, 0, 0.0015);
  border: 1px solid #d9d9d9;
  color: #595959;
}

.button {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding: 8px 16px;
  border-radius: 2px;
  cursor: pointer;
  font-size: 16px;
  line-height: 24px;
  background-color: #1890FF;
  border: 1px solid #1890FF;
  color: #ffffff;
  margin-right: 10px;
}

.button img {
  margin-right: 5px;
}

.button:last-child {
  margin-right: 0;
}

@media (max-width: 690px) {
  .header {
    flex-wrap: wrap;
  }
  .header .search {
    order: 5;
    margin-top: 20px;
    flex-basis: 100%;
  }
}

@media (max-width: 500px) {
  .header .buttons .btn-text {
    display: none;
  }
  .header .buttons .button img {
    margin-right: 0;
  }
}

.main .intro {
  box-shadow: 0px 7px 12px rgba(158, 158, 163, 0.1);
  height: 300px;
  background-color: #FFF1B8;
  border-radius: 10px;
  background-image: url(../img/pizza.png);
  background-repeat: no-repeat;
  background-position: top -10px right -160px;
  background-size: 570px;
  margin-bottom: 56px;
}

.main .intro .intro-info {
  position: relative;
  width: 43%;
  left: 7%;
  top: 20%;
}

.main .intro .intro-info .intro-header {
  font-family: "Roboto";
  color: #302c34;
  font-size: 39px;
  line-height: 46px;
  font-weight: bold;
  margin-bottom: 15px;
}

.main .intro .intro-info .intro-text {
  font-family: "Roboto";
  color: #302c34;
  font-size: 24px;
  line-height: 28px;
}

@media (max-width: 1100px) {
  .main .intro .intro-info .intro-header {
    font-size: 30px;
  }
  .main .intro .intro-info .intro-text {
    font-size: 18px;
  }
}

@media (max-width: 768px) {
  .main .intro {
    background-size: 450px;
    background-position: top -30px right -160px;
  }
  .main .intro .intro-info .intro-header {
    font-size: 25px;
    line-height: 30px;
  }
  .main .intro .intro-info .intro-text {
    font-size: 16px;
  }
}

@media (max-width: 690px) {
  .main .intro {
    background-size: 350px;
    background-position: top -20px right -140px;
  }
  .main .intro .intro-info .intro-header {
    font-size: 20px;
    line-height: 25px;
  }
  .main .intro .intro-info .intro-text {
    font-size: 14px;
  }
}

@media (max-width: 500px) {
  .main .intro {
    background-size: 250px;
    background-position: top -60px right -100px;
  }
  .main .intro .intro-info {
    width: 50%;
    top: 17%;
    left: 10%;
  }
}

.section-restaraunts .section-title {
  display: flex;
  flex-direction: row;
  justify-content: normal;
  align-items: center;
  margin-bottom: 44px;
}

.section-restaraunts .section-title .heading {
  font-family: "Roboto";
  color: #000000;
  font-size: 36px;
  line-height: 42px;
  font-weight: bold;
  margin-right: 30px;
}

.section-restaraunts .section-title .input-search {
  padding-top: 8px;
  padding-bottom: 8px;
  margin-left: auto;
  background: url(../img/search.svg);
  background-position: 10px center;
  background-repeat: no-repeat;
  flex-basis: 45%;
}

.section-restaraunts .food-cards {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: flex-start;
  flex-wrap: wrap;
}

.section-restaraunts .food-cards .card {
  background: #ffffff;
  cursor: pointer;
  transition: all 0.3s linear;
  box-shadow: 0px 4px 12px rgba(0, 0, 0, 0.05);
  border-radius: 0px 0px 7px 7px;
  flex-basis: 31%;
  margin-bottom: 30px;
}

.section-restaraunts .food-cards .card .dish-item {
  width: 100%;
  border-top-right-radius: 7px;
  border-top-left-radius: 7px;
  object-fit: cover;
}

.section-restaraunts .food-cards .card .food-info {
  padding: 20px 24px 34px 24px;
}

.section-restaraunts .food-cards .card .food-info .food-title {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 10px;
}

.section-restaraunts .food-cards .card:last-child {
  margin-bottom: 90px;
}

.section-restaraunts .food-cards .card:hover {
  transition: all 0.1s linear;
  transform: scale(1.02);
}

.other-info .rating {
  font-family: "Roboto";
  color: #ffc107;
  font-size: 18px;
  line-height: 32px;
  font-weight: bold;
  margin-right: 26px;
}

.other-info .rating img {
  margin-right: 7px;
}

.other-info .info {
  font-family: "Roboto";
  color: #8c8c8c;
  font-size: 18px;
  line-height: 32px;
}

.other-info .price::after {
  content: "";
  display: inline-block;
  width: 5px;
  height: 5px;
  background-color: #8c8c8c;
  border-radius: 50%;
  margin: 0 10px;
  vertical-align: middle;
}

.heading {
  font-family: "Roboto";
  color: #000000;
  font-size: 24px;
  line-height: 32px;
  font-weight: bold;
}

.order-title .heading {
  margin-bottom: 10px;
}

.order-title .ingridients {
  display: block;
  font-family: "Roboto";
  color: #8c8c8c;
  font-size: 18px;
  line-height: 21px;
  margin-bottom: 20px;
}

.order-info {
  display: flex;
  flex-direction: row;
  justify-content: normal;
  align-items: center;
}

.order-info .order-btn {
  margin-right: 32px;
}

.order-info .order-btn .btn-text {
  margin-right: 10px;
}

.order-info .order-price {
  font-family: "Roboto";
  color: black;
  font-size: 20px;
  line-height: 32px;
  font-weight: bold;
}

.span-tag {
  background-color: black;
  color: #ffffff;
  border-radius: 2px;
  padding: 1px 8px;
  font-size: 12px;
  line-height: 20px;
}

@media (max-width: 1350px) {
  .section-restaraunts .food-cards .card .food-info .other-info .rating {
    font-size: 14px;
    margin-right: 5px;
  }
  .section-restaraunts .food-cards .card .food-info .other-info .info {
    font-size: 14px;
  }
}

@media (max-width: 1100px) {
  .section-restaraunts .food-cards .card .food-info .other-info .info {
    font-size: 13px;
  }
}

@media (max-width: 940px) {
  .section-restaraunts .section-title .heading {
    font-size: 29px;
  }
  .section-restaraunts .food-cards .card {
    flex-basis: 48%;
  }
  .section-restaraunts .food-cards .card .food-info .other-info .rating {
    font-size: 17px;
    margin-right: 20px;
  }
  .section-restaraunts .food-cards .card .food-info .other-info .info {
    font-size: 17px;
  }
}

@media (max-width: 768px) {
  .section-restaraunts .section-title .heading {
    font-size: 27px;
  }
  .section-restaraunts .food-cards .card .food-info .other-info .rating {
    display: block;
    width: 100%;
  }
}

@media (max-width: 690px) {
  .section-restaraunts .section-title {
    flex-wrap: wrap;
  }
  .section-restaraunts .section-title .heading {
    font-size: 25px;
    margin-bottom: 15px;
  }
  .section-restaraunts .section-title .input-search {
    flex-basis: 100%;
  }
  .section-restaraunts .food-cards {
    justify-content: center;
  }
  .section-restaraunts .food-cards .card {
    flex-basis: 100%;
  }
  .section-restaraunts .food-cards .card .food-info .other-info .rating {
    display: block;
    width: 100%;
  }
  .section-restaraunts .food-cards .card:last-child {
    margin-bottom: 30px;
  }
}

.footer {
  background-color: #ffffff;
}

.footer .footer-info {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding: 68px 0px;
}

.footer .footer-info .navigation {
  display: flex;
  flex-direction: row;
  justify-content: normal;
  align-items: center;
}

.footer .footer-info .navigation .links-list {
  display: flex;
  flex-direction: row;
  justify-content: normal;
  align-items: center;
  flex-wrap: wrap;
}

.footer .footer-info .navigation .links-list .link {
  margin-right: 15px;
}

.footer .footer-info .navigation .links-list .link a {
  transition: color 0.2s linear;
  font-family: "Roboto";
  color: #595959;
  font-size: 18px;
  line-height: 21px;
}

.footer .footer-info .navigation .links-list .link a:hover {
  transition: color 0.2s linear;
  color: #ffc107;
}

.footer .footer-info .navigation img {
  margin-right: 35px;
}

.footer .footer-info .contact-us {
  display: flex;
  flex-direction: row;
  justify-content: normal;
  align-items: center;
}

.footer .footer-info .contact-us a {
  transition: all 0.2s linear;
  margin-right: 21px;
}

.footer .footer-info .contact-us a:hover {
  transition: all 0.2s linear;
  transform: scale(1.06);
}

@media (max-width: 1100px) {
  .footer .footer-info .navigation .links-list .link a {
    font-size: 16px;
  }
}

@media (max-width: 768px) {
  .footer .footer-info .navigation .links-list .link {
    flex-basis: 30%;
  }
}

@media (max-width: 690px) {
  .footer .footer-info {
    display: flex;
    flex-direction: column;
    justify-content: normal;
    align-items: center;
    padding: 25px 0px;
  }
  .footer .footer-info .navigation {
    display: flex;
    flex-direction: column;
    justify-content: normal;
    align-items: center;
    margin-bottom: 25px;
  }
  .footer .footer-info .navigation img {
    margin-right: 0;
    margin-bottom: 25px;
  }
  .footer .footer-info .navigation .links-list {
    display: flex;
    flex-direction: column;
    justify-content: normal;
    align-items: center;
  }
  .footer .footer-info .navigation .links-list .link {
    flex-basis: 20%;
    margin-right: 0;
    margin-bottom: 15px;
  }
}

.modal {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.4);
  display: none;
}

.modal .modal-dialog {
  background-color: #ffffff;
  padding: 41px 45px;
  max-width: 780px;
  width: 95%;
  min-height: 552px;
  margin: auto;
  border-radius: 5px;
}

.modal .modal-dialog .modal-header {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 45px;
}

.modal .modal-dialog .modal-header .title {
  font-family: "Roboto";
  color: black;
  font-size: 36px;
  line-height: 42px;
  font-weight: bold;
}

.modal .modal-dialog .modal-header .cancel {
  cursor: pointer;
  background-color: transparent;
  font-size: 37px;
  border: none;
}

.modal .modal-dialog .modal-body {
  overflow-y: scroll;
  height: 300px;
  margin-bottom: 56px;
}

.modal .modal-dialog .modal-body .food-row {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  border-bottom: 1px solid #d9d9d9;
  padding-bottom: 8px;
  padding-right: 15px;
  margin-bottom: 15px;
}

.modal .modal-dialog .modal-body .food-row .food-name {
  font-family: "Roboto";
  color: black;
  font-size: 18px;
  line-height: 32px;
}

.modal .modal-dialog .modal-body .food-row .counter-block {
  display: flex;
  flex-direction: row;
  justify-content: normal;
  align-items: center;
}

.modal .modal-dialog .modal-body .food-row .counter-block .price {
  font-family: "Roboto";
  color: black;
  font-size: 20px;
  line-height: 32px;
  font-weight: bold;
  margin-right: 47px;
}

.modal .modal-dialog .modal-body .food-row .counter-block .counter-btn {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  font-size: 14px;
  line-height: 22px;
  cursor: pointer;
  background-color: transparent;
  width: 39px;
  height: 32px;
  color: #40a9ff;
  border: 1px solid #40a9ff;
  border-radius: 3px;
}

.modal .modal-dialog .modal-body .food-row .counter-block .amounth {
  margin: 0px 13px;
  font-family: "Roboto";
  color: black;
  font-size: 16px;
  line-height: 24px;
}

.modal .modal-dialog .modal-footer {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

.modal .modal-dialog .modal-footer .final-price {
  font-family: "Roboto";
  color: #ffffff;
  font-size: 20px;
  line-height: 23px;
  font-weight: bold;
  padding: 15px 20px;
  border-radius: 5px;
}

.open-modal {
  display: flex;
}

* {
  box-sizing: border-box;
}

html {
  overflow-x: hidden;
}

body {
  background: linear-gradient(180deg, rgba(245, 245, 245, 0) 1.04%, #F5F5F5 100%);
}

.input {
  background-color: #ffffff;
  border: 1px solid #d9d9d9;
  border-radius: 2px;
  padding-left: 35px;
  font-size: 16px;
  line-height: 24px;
}

.container {
  margin: auto;
  max-width: 1200px;
}

@media (max-width: 1350px) {
  .container {
    max-width: 1000px;
  }
}

@media (max-width: 1100px) {
  .container {
    max-width: 900px;
  }
}

@media (max-width: 940px) {
  .container {
    max-width: 780px;
  }
}

@media (max-width: 768px) {
  .container {
    max-width: 650px;
  }
}

@media (max-width: 690px) {
  .container {
    max-width: 90%;
  }
}
