.header{
    @include flexMixin(row,space-between,center);
    margin-top: 44px;
    margin-bottom: 40px;
    .logo{
        transition: all 0.3s linear;
       &::before{
        content: "";
        display: inline-block;
        background-color: #ffc107;
        width: 3px;
        height: 0px;
        margin-right: 5px;
       }
       &:hover{
           &::before{
               transition: all 0.3s linear;
               height: 35px;
           }
       }
    }
    .search{
        flex: 0.8;
        height: 40px;
        background-image: url(../img/home.svg);
        background-repeat: no-repeat;
        background-position: 10px center;
    }
}
.buttons{
    @include flexMixin(row,space-between,center);
    .grey-btn,
    .basket-btn{
        background-color: transparent;
        box-shadow: 0px 0px 2px rgba(0, 0, 0, 0.0015);
        border: 1px solid $borderColor;
        color: #595959;
    }
}  
.button{
    @include flexMixin(row,space-between,center);
    padding: 8px 16px;
    border-radius: 2px;
    cursor: pointer;
    font-size: 16px;
    line-height: 24px;
    background-color: $BtnBg;
    border: 1px solid $BtnBg;
    color: $defaultColor;
    margin-right: 10px;
    img{
        margin-right: 5px;
    }
    &:last-child{
        margin-right: 0;
    }
}
@media (max-width:690px){
    .header{
        flex-wrap: wrap;
        .search{
            order: 5;
            margin-top: 20px;
            flex-basis: 100%;
        }
    }
}
@media (max-width:500px){
    .header{
        .buttons{
            .btn-text{
                display: none;
            }
            .button{
                img{
                    margin-right: 0;
                }
            }
        }
    }
}