@import './reset';
@import './colors';
@import './fonts';
@import './mixins';
// 
@import './header';
@import './intro';
@import './restaraunts';
@import './footer';
@import './modal';
*{
    box-sizing: border-box;
}

html{
    overflow-x: hidden;
}
body{
    background: linear-gradient(180deg, rgba(245, 245, 245, 0) 1.04%, #F5F5F5 100%);
}
.input{
    background-color: $defaultColor;
    border: 1px solid $borderColor;
    border-radius: 2px;
    padding-left: 35px;
    font-size: 16px;
    line-height: 24px;
}
.container{
    margin: auto;
    max-width: 1200px;
    @media (max-width:1350px){
        max-width: 1000px;
    }
    @media (max-width:1100px){
        max-width: 900px;
    }
    @media (max-width:940px){
        max-width: 780px;
    }
    @media (max-width:768px){
        max-width: 650px;
    }
    @media (max-width:690px){
        max-width: 90%;
    }
}