.modal {
  @include flexMixin(row, center, center);
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.4);
  display: none;
  .modal-dialog {
    background-color: $defaultColor;
    padding: 41px 45px;
    max-width: 780px;
    width: 95%;
    min-height: 552px;
    margin: auto;
    border-radius: 5px;
    .modal-header {
      @include flexMixin(row, space-between, center);
      margin-bottom: 45px;
      .title {
        @include textMixin($robotoFont, black, 36px, 42px);
        font-weight: bold;
      }
      .cancel {
        cursor: pointer;
        background-color: transparent;
        font-size: 37px;
        border: none;
      }
    }
    .modal-body {
      overflow-y: scroll; //
      height: 300px; //
      margin-bottom: 56px;
      .food-row {
        @include flexMixin(row, space-between, center);
        border-bottom: 1px solid $borderColor;
        padding-bottom: 8px;
        padding-right: 15px;
        margin-bottom: 15px;
        .food-name {
          @include textMixin($robotoFont, black, 18px, 32px);
        }
        .counter-block {
          @include flexMixin(row, normal, center);
          .price {
            @include textMixin($robotoFont, black, 20px, 32px);
            font-weight: bold;
            margin-right: 47px;
          }
          .counter-btn {
            @include flexMixin(row, center, center);
            font-size: 14px;
            line-height: 22px;
            cursor: pointer;
            background-color: transparent;
            width: 39px;
            height: 32px;
            color: #40a9ff;
            border: 1px solid #40a9ff;
            border-radius: 3px;
          }
          .amounth {
            margin: 0px 13px;
            @include textMixin($robotoFont, black, 16px, 24px);
          }
        }
      }
    }
    .modal-footer {
      @include flexMixin(row, space-between, center);
      .final-price {
        @include textMixin($robotoFont, $defaultColor, 20px, 23px);
        font-weight: bold;
        padding: 15px 20px;
        border-radius: 5px;
      }
    }
  }
}
.open-modal{
    display: flex;
}
