/*================================*\
    reset.less
\*================================*/
body {
    text-align: left;
    background: #fff;
    color: #000;
  }
  sup,
  sub {
    vertical-align: inherit;
  }
  body,
  h1,
  h2,
  h3,
  h4,
  h5,
  h6,
  hr,
  p,
  figure,
  blockquote,
  pre,
  ol,
  ul,
  dl,
  dd,
  fieldset {
    margin: 0;
  }
  ol,
  ul,
  th,
  td,
  legend {
    padding: 0;
  }
  h1,
  h2,
  h3,
  h4,
  h5,
  h6,
  address,
  pre,
  th,
  em,
  strong,
  i,
  small,
  dfn,
  b,
  var,
  cite,
  sup,
  sub,
  code,
  kbd,
  samp {
    font: inherit;
  }
  caption,
  th {
    text-align: inherit;
  }
  pre {
    white-space: inherit;
  }
  mark {
    background: transparent;
  }
  a,
  u,
  s,
  ins,
  del {
    text-decoration: none;
  }
  ol,
  ul {
    list-style: none;
  }
  q {
    quotes: none;
  }
  a,
  mark {
    color: inherit;
  }
  