.section-restaraunts {
  .section-title {
    @include flexMixin(row, normal, center);
    margin-bottom: 44px;
    .heading {
      @include textMixin($robotoFont, #000000, 36px, 42px);
      font-weight: bold;
      margin-right: 30px;
    }
    .input-search {
      padding-top: 8px;
      padding-bottom: 8px;
      margin-left: auto;
      background: url(../img/search.svg);
      background-position: 10px center;
      background-repeat: no-repeat;
      flex-basis: 45%;
    }
  }
  .food-cards {
    @include flexMixin(row, space-between, flex-start);
    flex-wrap: wrap;
    .card {
      background: #ffffff;
      cursor: pointer;
      transition: all 0.3s linear;
      box-shadow: 0px 4px 12px rgba(0, 0, 0, 0.05);
      border-radius: 0px 0px 7px 7px;
      flex-basis: 31%;
      margin-bottom: 30px;
      .dish-item {
        width: 100%;
        border-top-right-radius: 7px;
        border-top-left-radius: 7px;
        object-fit: cover;
      }
      .food-info {
        padding: 20px 24px 34px 24px;
        .food-title {
          @include flexMixin(row, space-between, center);
          margin-bottom: 10px;
        }
      }
      &:last-child {
        margin-bottom: 90px;
      }
      &:hover {
        transition: all 0.1s linear;
        transform: scale(1.02);
      }
    }
  }
}
.other-info {
  .rating {
    @include textMixin($robotoFont, #ffc107, 18px, 32px);
    font-weight: bold;
    margin-right: 26px;
    img {
      margin-right: 7px;
    }
  }
  .info {
    @include textMixin($robotoFont, #8c8c8c, 18px, 32px);
  }
  .price {
    &::after {
      content: "";
      display: inline-block;
      width: 5px;
      height: 5px;
      background-color: #8c8c8c;
      border-radius: 50%;
      margin: 0 10px;
      vertical-align: middle;
    }
  }
}

.heading {
  @include textMixin($robotoFont, #000000, 24px, 32px);
  font-weight: bold;
}
.order-title {
  .heading {
    margin-bottom: 10px;
  }
  .ingridients {
    display: block;
    @include textMixin($robotoFont, #8c8c8c, 18px, 21px);
    margin-bottom: 20px;
  }
}
.order-info {
  @include flexMixin(row, normal, center);
  .order-btn {
    margin-right: 32px;
    .btn-text {
      margin-right: 10px;
    }
  }
  .order-price {
    @include textMixin($robotoFont, black, 20px, 32px);
    font-weight: bold;
  }
}
.span-tag {
  background-color: black;
  color: $defaultColor;
  border-radius: 2px;
  padding: 1px 8px;
  font-size: 12px;
  line-height: 20px;
}
@media (max-width: 1350px) {
  .section-restaraunts {
    .food-cards {
      .card {
        .food-info {
          .other-info {
            .rating {
              font-size: 14px;
              margin-right: 5px;
            }
            .info {
              font-size: 14px;
            }
          }
        }
      }
    }
  }
}
@media (max-width: 1100px) {
  .section-restaraunts {
    .food-cards {
      .card {
        .food-info {
          .other-info {
            .info {
              font-size: 13px;
            }
          }
        }
      }
    }
  }
}
@media (max-width: 940px) {
  .section-restaraunts {
    .section-title {
      .heading {
        font-size: 29px;
      }
    }
    .food-cards {
      .card {
        flex-basis: 48%;
        .food-info {
          .other-info {
            .rating {
              font-size: 17px;
              margin-right: 20px;
            }
            .info {
              font-size: 17px;
            }
          }
        }
      }
    }
  }
}
@media (max-width: 768px) {
  .section-restaraunts {
    .section-title {
      .heading {
        font-size: 27px;
      }
    }
    .food-cards {
      .card {
        .food-info {
          .other-info {
            .rating {
              display: block;
              width: 100%;
            }
          }
        }
      }
    }
  }
}
@media (max-width: 690px) {
  .section-restaraunts {
    .section-title {
      flex-wrap: wrap;
      .heading {
        font-size: 25px;
        margin-bottom: 15px;
      }
      .input-search {
        flex-basis: 100%;
      }
    }
    .food-cards {
      justify-content: center;
      .card {
        flex-basis: 100%;
        .food-info {
          .other-info {
            .rating {
              display: block;
              width: 100%;
            }
          }
        }
        &:last-child {
          margin-bottom: 30px;
        }
      }
    }
  }
}
